import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { AreaManagerService } from '../area-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { filter, takeUntil } from 'rxjs/operators';
import { AddOrUpdateAreaComponent } from '../add-area/add-area.component';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';

@Component({
  selector: 'app-area-list',
  templateUrl: './area-list.component.html'
})
export class AreaListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public areaManagerService: AreaManagerService, private ModalService: NgbModal) {
  }

  ngOnInit(): void {
    this.areaManagerService.getAreaList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addAreaModal(): void {
    const modalRef = this.ModalService.open(AddOrUpdateAreaComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const area = {
      area: '',
      status: 'Active'
    }
    modalRef.componentInstance.area = area;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.areaManagerService.getAreaList());
  }

  deleteArea(area: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.area = area;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.areaManagerService.getAreaList());
  }

  updateArea(area: any): void {
    const modalRef = this.ModalService.open(AddOrUpdateAreaComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.area = area;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.areaManagerService.getAreaList());
  }
}
