import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthManagerService } from 'src/app/auth/auth-manager.service';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AreaManagerService } from '../area-manager.service';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-add-area',
  templateUrl: './add-area.component.html'
})
export class AddOrUpdateAreaComponent implements OnInit , OnDestroy {
  private ngUnsubcribe = new Subject();
  areaForm: FormGroup;
  logginIn = false;
  error: string;

  @Input()
  area: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     private areaManager: AreaManagerService
    // private authManagerService: AuthManagerService,
    // private route: ActivatedRoute,
    // private authUserService: AuthUserService

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  ngOnInit(): void {
    this.areaForm = this.formBuilder.group({
      _id : this.area._id,
      area: [this.area.area, [Validators.required]],
      status: [this.area.status, Validators.required]
    });
  }

  submit(): void {
    if (this.operation == 'CREATE') {
      this.areaManager.addArea(this.areaForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    } else {
      this.areaManager.updateArea(this.areaForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    }
  }

  cancel(): void {
    this.activeModal.close(false);
  }
}
