import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { AreaService } from '../core/area.service';
import { TableState } from '../core/table-state';

@Injectable()
export class AreaManagerService {
  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];

  private _areaList$ = new BehaviorSubject<any[]>(null);
  get areaList$(): Observable<any[]> {
    return this._areaList$.asObservable();
  }

  constructor(private areaService: AreaService) { }

  addArea(area: any): Observable<any> {
    // const area = {
    //   name: area['name'],
    //   email: area['email'],
    //   password: area['password'],
    //   address: area['address'],
    //   city: area['city'],
    //   mobile: area['mobile'],
    //   status: area['status']
    // };
    return this.areaService.addArea(area);
  }

  updateArea(area: any): Observable<any> {
    // const area = {
    //   _id: area['_id'],
    //   name: area['name'],
    //   email: area['email'],
    //   address: area['address'],
    //   city: area['city'],
    //   mobile: area['mobile'],
    //   status: area['status']
    // };
    return this.areaService.updateArea(area);
  }

  deleteArea(id: string): Observable<any> {
    return this.areaService.deleteArea(id);
  }

  getArea(id: string): Observable<any> {
    return this.areaService.getArea(id);
  }


  getAreaList(): void {
    this.areaService.getAreaList()
      .subscribe((data: any[]) => {
        console.log('Got Areas for you: ', data);
        this._areaList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._areaList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['area'] && item['area'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm));
      this._areaList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}